.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1a1a2e;
    transition: background-color 0.3s ease; 
  }
  
  .home-container h1 {
    font-size: 3rem;
    color: #e0e0e0;
    transition: color 0.3s ease;
  }
  
  .home-container h1:hover {
    color: #f59e0b; 
  }
  