.about-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1a1a2e;
    text-align: center;
    transition: background-color 0.3s ease;
  }
  
  .about-container h1 {
    font-size: 2.5rem;
    color: #e0e0e0;
    transition: color 0.3s ease; 
  }
  
  .about-container h1:hover {
    color: #f59e0b; 
  }
  
  .about-container p {
    font-size: 1.2rem;
    color: #b3b3b3;
    transition: color 0.3s ease;
  }
  
  .about-container p:hover {
    color: #f59e0b; 
  }
  