.navbar {
    background-color: #0d1b2a; 
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    transition: background-color 0.3s ease; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  }
  
  .navbar-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    padding: 0 20px;
  }
  
  .navbar-logo {
    margin: .5rem;
    font-size: 2rem;
    font-weight: bold;
    color: #f59e0b; 
    text-decoration: none;
    transition: color 0.3s ease; 
  }
  
  .navbar-logo:hover {
    color: #ffba08; 
  }
  
  .nav-menu {
    display: flex;
    list-style: none;
  }
  
  .nav-item {
    margin-left: 20px;
  }
  
  .nav-links {
    color: #f59e0b; 
    padding: 10px 15px;
    border: 2px solid transparent; 
    border-radius: 5px; 
    text-decoration: none; 
    transition: color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
    position: relative; 
    background: transparent;
    font-size: 1.5rem;
  }
  
  .nav-links:hover {
    color: white; 
    transform: translateY(-3px); 
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3); 
  }
  
  .nav-links::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5)); 
    opacity: 0; 
    transition: opacity 0.3s ease; 
    border-radius: 5px; 
  }
  
  .nav-links:hover::after {
    opacity: 1;
  }
  